'use strict';

export default getGeoPosition()
  .then(location => {
    console.log(location);
  })
  .catch(error => {
    console.log(error);
  });

function getGeoPosition() {
  const options = {
    maximumAge: 30000,
  };
  return new Promise((resolve, reject) => {
    navigator.geolocation.getCurrentPosition(resolve, reject, options);
  });
}
